import { EventEmitter } from '@pixi/utils';
import { HtmlAudio } from './HtmlAudio';
import { WebAudio } from './WebAudio';
import { IAudioOption, IAudio } from 'vfInterface';
/**
 * 负责在整个应用程序中播放，同步和分析声音。
 * @class
 * @extends PIXI.utils.EventEmitter
 * @memberof PIXI
 */
export class AudioEngine extends EventEmitter {
    /**
     * 定义音频使用的类型
     *
     * @static
     * @default 'auto'
     */
    public static type: 'auto' | 'audio' | 'webaudio' = 'auto';

    private static share: AudioEngine;

    /**
     * 定义音频使用的类型
     *
     * @static
     * @memberof PIXI.AudioEngine
     * @function Ins
     * @returns {PIXI.AudioEngine}
     */
    public static Ins(): AudioEngine {
        if (AudioEngine.share) {
            return AudioEngine.share;
        }
        AudioEngine.share = new AudioEngine();

        return AudioEngine.share;
    }

    private _audioContext: any = null;// AudioContext
    private _audioContextInitialized = false;
    private _muteButton: HTMLButtonElement = null;

    /**
     * 当前存放的音频列表
     * @type {Map<string, vf.IAudio>}
     */
    public readonly map = new Map<string, IAudio>();
    /**
     * 获取当前主机是否支持Web Audio，从而可以创建AudioContexts。
     */
    public canUseWebAudio = false;

    /**
     * 主增益节点定义音频的全局音量。
     */
    public masterGain: GainNode;

    /**
     * 定义如果不支持WebAudio，Babylon是否应发出警告。
     * @ignoreNaming
     */
    public WarnedWebAudioUnsupported = false;

    /**
     * 获取您的浏览器是否支持mp3。
     */
    public readonly isMP3supported: boolean;

    /**
     * 获取浏览器是否支持ogg。
     */
    public readonly isOGGsupported: boolean;

    /**
    * 获取音频是否已在设备上解锁。
    * 有些浏览器对音频有很强的限制，除非是自动播放
    * 用户互动已经发生。
        */
    public unlocked = true;

    /**
     * 定义音频引擎是否依赖于自定义解锁按钮。
     * 在这种情况下，不会显示嵌入按钮。
     */
    public useCustomUnlockedButton = false;

    /**
     * 音效被中断
     * @default 'audioLockedInterrupted'
     */
    public readonly audioLockedInterrupted = 'audioLockedInterrupted';
    /**
     * 在浏览器上解锁音频时引发的事件。
     * @default 'audioUnlockedObservable'
     */
    public readonly audioUnlockedObservable = 'audioUnlockedObservable';

    /**
     * 音频锁定在浏览器上时引发的事件。
     * @default 'audioLockedObservable'
     */
    public readonly audioLockedObservable = 'audioLockedObservable';

    /**
     * 不可实例化
     */
    constructor() {
        super();

        const audioElem = document.createElement('audio');

        try {
            // eslint-disable-next-line max-len
            if (audioElem && !!audioElem.canPlayType && audioElem.canPlayType('audio/mpeg; codecs="mp3"').replace(/^no$/, '')) {
                this.isMP3supported = true;
            }
        }
        catch (e) {
            // 功能检查异常.
        }

        try {
            // eslint-disable-next-line max-len
            if (audioElem && !!audioElem.canPlayType && audioElem.canPlayType('audio/ogg; codecs="vorbis"').replace(/^no$/, '')) {
                this.isOGGsupported = true;
            }
        }
        catch (e) {
            // 功能检查异常.
        }
    }

    /**
    * 建立音频
    * vf.AudioEngine.type = 'audio｜webaudio|auto'; // 设置音频类型
    * vf.AudioEngine.Ins().useCustomUnlockedButton = true | false; // 默认false开启
    * const audio = new vf.AudioEngine.Ins().createAudio('audioName', audioUrl,{autoplay:true});
    * audio.play();
    * @param {string} name - 全局唯一名
    * @param {string | ArrayBuffer} [urlOrArrayBuffer] - url音频路径
    * @param {vf.IAudioOption} [options] 配置
    * @return {vf.IAudio}
    */
    public createAudio(name: string, urlOrArrayBuffer: any, options?: IAudioOption): IAudio {
        let auido: IAudio;
        const w = window as any;

        if (this.map.has(name)) {
            return this.map.get(name);
        }
        if (typeof w.AudioContext !== 'undefined' || typeof w.webkitAudioContext !== 'undefined') {
            w.AudioContext = w.AudioContext || w.webkitAudioContext;
            this.canUseWebAudio = true;
        }
        let type = AudioEngine.type;

        if (type === 'auto') {
            if (this.canUseWebAudio) {
                type = 'webaudio';
            }
            else {
                type = 'auto';
            }
        }

        switch (type) {
            case 'audio':
                auido = new HtmlAudio(name, urlOrArrayBuffer, options);
                break;
            case 'webaudio':
                auido = new WebAudio(name, urlOrArrayBuffer, options);
                (auido as WebAudio).connectToSoundTrackAudioNode(this.masterGain);
                break;
            default:
                auido = new HtmlAudio(name, urlOrArrayBuffer, options);
        }
        this.map.set(name, auido);

        return auido;
    }

    /**
     * 获取当前的AudioContext（如果可用）。
     */
    public get audioContext(): AudioContext {
        if (!this._audioContextInitialized) {
            this._init();
        }

        return this._audioContext;
    }

    /**
     * 标记处于锁定状态的音频引擎 (浏览器阻止音频自动播放)。
     */
    public lock(): void {
        this._triggerSuspendedState();
        if (!this.unlocked && !this._muteButton) {
            this._displayMuteButton();
        }
    }

    /**
    * 在dom上完成用户操作后，解锁音频引擎。
    */
    public unlock(): void {
        this._triggerRunningState();
    }

    public resumeAudioContext(): Promise<void> {
        let result: Promise<void>;

        if (this._audioContext.resume) {
            result = this._audioContext.resume();
        }

        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        return result! || Promise.resolve();
    }

    private _init(): void {
        try {
            if (this.canUseWebAudio) {
                const audioContext = this._audioContext = new AudioContext();
                const masterGain = this.masterGain = audioContext.createGain();

                masterGain.gain.value = 1;
                masterGain.connect(audioContext.destination);
                this._audioContextInitialized = true;
                if (audioContext.state === 'running') {
                    this._triggerRunningState();
                }
            }
        }
        catch (e) {
            this.canUseWebAudio = false;
            // eslint-disable-next-line no-console
            console.log(`Web Audio: ${e.message}`);
        }
    }

    private _tryToRun = false;
    private _triggerRunningState(): void {
        if (this._tryToRun) {
            return;
        }
        this._tryToRun = true;
        if (this.audioContext) {
            this.resumeAudioContext()
                .then(() => {
                    this._tryToRun = false;
                    if (this._muteButton) {
                        this._hideMuteButton();
                    }
                }).catch(() => {
                    this._tryToRun = false;
                    this.unlocked = false;
                });
        } else if (this._muteButton) {
            this._hideMuteButton();
        }

        // Notify users that the audio stack is unlocked/unmuted
        this.unlocked = true;
        this.emit(this.audioUnlockedObservable, this);
    }

    private _triggerSuspendedState(): void {
        this.unlocked = false;
        this.emit(this.audioLockedObservable, this);
    }

    /**
     * 销毁并释放与音频ccontext相关的资源。
     */
    public dispose(): void {
        this.map.forEach((value) => {
            value.dispose();
        });

        if (this.canUseWebAudio && this._audioContextInitialized) {
            if (this._audioContext) {
                this.masterGain.disconnect();
                this.masterGain.connect(this._audioContext.destination);
            }
            this.masterGain.gain.value = 1;
        }
        this.WarnedWebAudioUnsupported = false;
        this._hideMuteButton();
        this.removeAllListeners();
    }

    /**
     * 获取主增益上的全局音量。
     * @returns 不存在返回-1
     */
    public getGlobalVolume(): number {
        if (this.canUseWebAudio && this._audioContextInitialized) {
            return this.masterGain.gain.value;
        }

        return -1;
    }

    /**
     * 设置全音局量（设置主增益）。
     * @param newVolume newVolume定义应用程序的新全局卷
     */
    public setGlobalVolume(newVolume: number): void {
        if (this.canUseWebAudio && this._audioContextInitialized) {
            this.masterGain.gain.value = newVolume;
        }
    }

    private _displayMuteButton(): void {
        if (this.useCustomUnlockedButton || this._muteButton) {
            return;
        }

        this._muteButton = document.createElement('button') as HTMLButtonElement;
        this._muteButton.className = 'vfUnmuteIcon';
        this._muteButton.id = 'vfUnmuteIconBtn';
        this._muteButton.title = 'Unmute';
        // eslint-disable-next-line max-len
        const imageUrl = 'data:image/svg+xml;charset=UTF-8,%3Csvg%20version%3D%221.1%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2239%22%20height%3D%2232%22%20viewBox%3D%220%200%2039%2032%22%3E%3Cpath%20fill%3D%22white%22%20d%3D%22M9.625%2018.938l-0.031%200.016h-4.953q-0.016%200-0.031-0.016v-12.453q0-0.016%200.031-0.016h4.953q0.031%200%200.031%200.016v12.453zM12.125%207.688l8.719-8.703v27.453l-8.719-8.719-0.016-0.047v-9.938zM23.359%207.875l1.406-1.406%204.219%204.203%204.203-4.203%201.422%201.406-4.219%204.219%204.219%204.203-1.484%201.359-4.141-4.156-4.219%204.219-1.406-1.422%204.219-4.203z%22%3E%3C%2Fpath%3E%3C%2Fsvg%3E';

        // eslint-disable-next-line max-len
        const css = `.vfUnmuteIcon { position: absolute; left: 20px; top: 20px; height: 40px; width: 60px; background-color: rgba(51,51,51,0.7); background-image: url(${imageUrl});  background-size: 80%; background-repeat:no-repeat; background-position: center; background-position-y: 4px; border: none; outline: none; transition: transform 0.125s ease-out; cursor: pointer; z-index: 9999; } .vfUnmuteIcon:hover { transform: scale(1.05) } .vfUnmuteIcon:active { background-color: rgba(51,51,51,1) }`;

        const style = document.createElement('style');

        style.appendChild(document.createTextNode(css));
        document.getElementsByTagName('head')[0].appendChild(style);

        document.body.appendChild(this._muteButton);

        this._muteButton.addEventListener('touchend', () => {
            this._triggerRunningState();
        }, true);
        this._muteButton.addEventListener('click', () => {
            this._triggerRunningState();
        }, true);
    }

    private _hideMuteButton(): void {
        if (this._muteButton) {
            document.body.removeChild(this._muteButton);
            this._muteButton = null;
        }
    }

    /**
     * 检测当前音频是否支持
     * @param url {number} 需要检测URL
     */
    public checkCanPlayType(url: string): boolean {
        let codecSupportedFound = false;

        if (url.substr(0, 4) === 'data') {
            codecSupportedFound = true;

            return codecSupportedFound;
        }
        if (url.indexOf('.mp3') !== -1 && this.isMP3supported) {
            codecSupportedFound = true;
        }
        if (url.indexOf('.ogg') !== -1 && this.isOGGsupported) {
            codecSupportedFound = true;
        }
        if (url.indexOf('.wav') !== -1) {
            codecSupportedFound = true;
        }
        if (url.indexOf('blob:') !== -1) {
            codecSupportedFound = true;
        }

        return codecSupportedFound;
    }
}
