import { Ticker } from '@pixi/ticker';
import { EventEmitter, arraybuffToBase64Sync } from '@pixi/utils';
import { IAudio, IAudioOption } from 'vfInterface';
import { AudioEngine } from './AudioEngine';

/**
 * HtmlAudio
 *
 *
 * @class
 * @extends PIXI.utils.EventEmitter
 * @memberof PIXI
 */
export class HtmlAudio extends EventEmitter implements IAudio {
    /**
     * 音频全局唯一名
     * @readonly
     * @member {string}
     */
    public readonly name: string;
    /**
     * 是否自动播放
     * @readonly
     * @default false
     * @member {string}
     */
    public readonly autoplay: boolean;
    private _audio: HTMLAudioElement;
    private _isReadyToPlay = false;
    private _isPlaying = false;
    private _isPause = false;
    private _startOffset = 0;
    private _playLength = 0;
    private _stopTime = 0;
    private _isDispose = false;

    /**
     * 完成播放一次后声音是否循环播放。
     * @default false
     * @member {boolean}
     */
    public get loop(): boolean {
        return this._audio.loop;
    }
    public set loop(value) {
        this._audio.loop = value;
    }

    /**
     * 播放的速率
     * @default 1
     * @member {number}
     */
    public get playbackRate(): number {
        return this._audio.playbackRate;
    }
    public set playbackRate(value) {
        this._audio.playbackRate = value;
    }

    /**
     * 音量(0.0 - 1.0)
     * @default 1
     * @member {number}
     */
    public get volume(): number {
        return this._audio.volume;
    }
    public set volume(value) {
        this._audio.volume = value;
    }

    /**
     * 获取当前音频的长度
     * @readonly
     * @default 0
     * @member {number}
     */
    public get duration(): number {
        return this._audio.duration;
    }

    /**
     * 获取当前音频的暂停状态
     * @readonly
     * @default 1
     * @member {number}
     */
    public get paused(): boolean {
        return this._isPause;
    }

    /**
     * 获取当前音频播放的时间
     * @readonly
     * @default 1
     * @member {number}
     */
    public get currentTime(): number {
        return this._audio.currentTime;
    }

    constructor(name: string, urlOrArrayBuffer: any, option?: IAudioOption) {
        super();
        this.name = name;
        this.autoplay = option.autoplay || false;
        this.init(urlOrArrayBuffer, option);
    }

    private async init(urlOrArrayBuffer: any, option?: IAudioOption): Promise<void> {
        if (typeof urlOrArrayBuffer !== 'string') { // arraybuff
            urlOrArrayBuffer = await arraybuffToBase64Sync(urlOrArrayBuffer);
            urlOrArrayBuffer = `data:audio/mp3;base64,${urlOrArrayBuffer}`;
        }
        this._audio = new Audio(urlOrArrayBuffer as string);
        const audio = this._audio;

        if (!AudioEngine.Ins().checkCanPlayType(urlOrArrayBuffer)) {
            audio.remove();
            throw new Error(`not support type ${urlOrArrayBuffer}`);
        }
        if (option) {
            audio.loop = option.loop || false;
            audio.volume = option.volume || 1;
            audio.playbackRate = option.playbackRate || 1;
        }
        audio.autoplay = false;
        audio.controls = false;
        audio.preload = 'auto';
        const oncanplay = this.oncanplay.bind(this);
        const onCanplaythrough = this.onCanplaythrough.bind(this);
        const onError = this.onError.bind(this);
        const onPause = this.onPause.bind(this);
        const onTimeupdate = this.onTimeupdate.bind(this);

        audio.oncanplay = oncanplay;
        audio.oncanplaythrough = onCanplaythrough;
        audio.onerror = onError;
        audio.onpaste = onPause;
        audio.ontimeupdate = onTimeupdate;
        audio.onended = (): void => {
            this._isPlaying = false;
            this.emit('ended', this);
        };

        document.body.appendChild(audio);

        if (this.autoplay) {
            this.play();
        }
    }

    private isCanPlay = false;
    private oncanplay(): void {
        const audio = this._audio;

        audio.oncanplay = undefined;
        this._isReadyToPlay = true;
        if (this._isPlaying) {
            this.isCanPlay = true;
            this.play(this._startOffset, this._playLength);
        }
        this.emit('canplay', this);
    }

    private onCanplaythrough(): void {
        this._audio.oncanplaythrough = undefined;
        this._isReadyToPlay = true;
        if (this._isPlaying && !this.isCanPlay) {
            this.play(this._startOffset, this._playLength);
        }
        this.emit('canplaythrough', this);
    }

    private onError(event: Event | string, source?: string, lineno?: number, colno?: number, error?: Error): void {
        if (error) {
            console.error(source, lineno, colno, error.message);
        }
        if (error && error.message) {
            this.emit('error', this, event);

            return;
        }
        this.emit('error', this, event);
    }

    private onPause(): void {
        this.emit('pause', this, this.paused);
    }

    private onTimeupdate(): void {
        this.emit('timeupdate', this, this._audio.currentTime);
    }

    private removeUpdateEvent(): void{
        Ticker.shared.remove(this.onStopUpdate, this);
        Ticker.shared.remove(this.onPlayLength, this);
        Ticker.shared.remove(this.onDelayPlay, this);
    }
    private onStopUpdate(): void {
        const audio = this._audio;
        const stopTime = this._stopTime;

        if (stopTime !== 0) {
            if (audio.currentTime >= stopTime) {
                audio.pause();
                this._stopTime = 0;
                this.removeUpdateEvent();
            }
        }
    }

    private playTime = 0;
    private onDelayPlay(): void{
        if (performance.now() > this.playTime) {
            Ticker.shared.remove(this.onDelayPlay, this);
            this.play(0, this._startOffset, this._playLength);
        }
    }

    private onPlayLength(): void {
        const playLength = this._playLength;
        const audio = this._audio;

        if (playLength !== 0) {
            if (audio.currentTime >= (playLength + this._startOffset)) {
                audio.pause();
                this._playLength = 0;
                this.removeUpdateEvent();
            }
        }
    }

    private audioLoadTimeId = -1;
    /**
     * 声音播放接口
     *
     *  await sound.play()
     *
     * @param {number} [offset] - 声音的开始偏移值
     * @param {number} [length] - 持续时间（以秒为单位）
     */
    play(time?: number, offset?: number, length?: number): void{
        clearTimeout(this.audioLoadTimeId);
        if (this._isDispose) {
            throw new Error('has been unloaded htmlaudio!');
        }
        if (!this._isPause) {
            this._startOffset = offset || 0;
            this._playLength = length || 0;
        }
        if (time) {
            this.playTime = performance.now() + (time * 1000);
            Ticker.shared.remove(this.onDelayPlay, this);
            Ticker.shared.add(this.onDelayPlay, this);

            return;
        }
        this.playTime = 0;
        this._isPause = false;
        this._isPlaying = true;
        if (this._isReadyToPlay) {
            try {
                this._audio.currentTime = this._startOffset;
            }
            catch (e) {
                //
            }
            finally {
                if (this._playLength) {
                    Ticker.shared.remove(this.onPlayLength, this);
                    Ticker.shared.add(this.onPlayLength, this);
                }
                const promise = this._audio.play();

                if (promise !== undefined) {
                    // eslint-disable-next-line
                    promise.then((_) => {
                        this.emit('play', this);
                    }).catch(() => {
                        const ae = AudioEngine.Ins();

                        ae.once(ae.audioUnlockedObservable, (): void => {
                            this.play(time, offset, length);
                        }, this);
                        ae.lock();
                    });
                }
                else {
                    this.emit('play', this);
                }
            }
        }
        else if (this._audio) {
            this._audio.load();
        }
        else {
            this.audioLoadTimeId = window.setTimeout(() => {
                this.play(time, offset, length);
            }, 30);
        }
    }
    /**
    * 停止声音
    * @param time (optional) X秒后停止声音。默认情况下立即停止
    */
    stop(time?: number): void {
        if (this._isDispose) {
            throw new Error('has been unloaded htmlaudio!');
        }
        if (!this._isPlaying) {
            return;
        }
        const audio = this._audio;

        this.removeUpdateEvent();
        this._isPlaying = false;
        this._startOffset = 0;
        this._playLength = 0;
        this._stopTime = time || 0;
        if (time) {
            this._stopTime = audio.currentTime + time;
            Ticker.shared.add(this.onStopUpdate, this);
        }
        else {
            audio.currentTime = 0;
            audio.pause();
        }
    }
    /**
     * 暂停声音
     */
    pause(): void {
        this.removeUpdateEvent();
        this._isPlaying = false;
        this._isPause = true;
        this._startOffset = this._audio.currentTime;
        this._audio.pause();
    }

    /**
     * 释放
     */
    dispose(): void {
        this.removeUpdateEvent();
        this._isDispose = true;
        const audio = this._audio;

        audio.oncanplay = undefined;
        audio.oncanplaythrough = undefined;
        audio.onerror = undefined;
        audio.onpaste = undefined;
        audio.ontimeupdate = undefined;
        audio.onended = undefined;
        if (audio) {
            audio.pause();
            audio.src = '';
            audio.load();
        }
        audio.remove();
        this._audio = null;
        AudioEngine.Ins().map.delete(this.name);
    }

    /**
     * 获取包含数据的当前基础音频缓冲区
     */
    getAudioBuffer(): AudioBuffer | undefined {
        return undefined;
    }
}
