/* eslint-disable max-len */
type EventLevel = 'info' | 'warning' | 'error';
interface IEvent {
    code: string;
    level: EventLevel;
    data: any;
    message?: string;
}

interface Options {
    timeout?: number;
    responseType?: '' | 'arraybuffer' | 'blob' | 'json' | 'text';
    method?: 'GET' | 'POST';
    errorCount?: number;
}

export const enum Code {
    ok = 1,
    jsonParseError,
    NetWorrError
}

// eslint-disable-next-line max-len
function removeEventListener<K extends keyof XMLHttpRequestEventTargetEventMap>(xhr: XMLHttpRequest, listener?: Array<[ K, (this: XMLHttpRequestEventTarget, ev: XMLHttpRequestEventTargetEventMap[K]) => any]>): void {
    if (listener) {
        listener.forEach((value) => {
            xhr.removeEventListener(value[0], value[1]);
        });
        listener.length = 0;
    }
    xhr.onload = null;
    xhr.onerror = null;
    xhr.onreadystatechange = null;
}

export function format(code: string | number, data: any, message?: string, level: EventLevel = 'error'): IEvent {
    if (Code.ok === code) {
        level = 'info';
    }
    code = code.toString();

    return { code, level, data, message };
}

export function parseJson(text: any,): IEvent {
    try {
        const data = ((typeof text) === 'string') ? JSON.parse(text) : text;

        return format(Code.ok, data, 'ok');
    }
    catch (e) {
        return format(Code.jsonParseError, e, e.message || '');
    }
}

export function geXHRtData(target: XMLHttpRequest): IEvent | undefined {
    const xhr = target;

    if (xhr.readyState === 4 && xhr.status >= 400) { return undefined; }

    try {
        const response = xhr.response || xhr.responseText;
        let msg: IEvent;

        switch (xhr.responseType) {
            case 'json':
                msg = parseJson(response);
                break;
            default:
                msg = format(Code.ok, response, 'ok');
        }

        return msg;
    }
    catch (e) {
        return format(Code.NetWorrError, e, `${xhr.responseURL}`);
    }
}

/**
 * 读取文件
 *
 * @memberof PIXI.utils
 * @function readFileSync
 * @param {string} url - 文件网络路径
 * @param {object} options - the defaultValue if no filename prefix is set.
 * @param {array} listener - resolution / device pixel ratio of an asset
 * @return {object|undefined}
 */
export function readFileSync<K extends keyof XMLHttpRequestEventTargetEventMap>(url: string, options: Options = {}, listener?: Array<[ K, (this: XMLHttpRequestEventTarget, ev: XMLHttpRequestEventTargetEventMap[K]) => any]>): Promise < any > {
    // eslint-disable-next-line
    return new Promise<any>((resolve, reject) => {
        const errorCount = { cur: 0, max: options.errorCount || 1 };
        const xhr = new XMLHttpRequest();

        const method = options.method || 'GET';

        xhr.timeout = options.timeout || 0;
        xhr.responseType = options.responseType || 'text';
        if (listener) {
            listener.forEach((value) => {
                xhr.addEventListener(value[0], value[1]);
            });
        }
        // eslint-disable-next-line
        xhr.onload = (evt: ProgressEvent) => {
            const msg = geXHRtData(evt.target as XMLHttpRequest);

            if (msg.code === Code.ok.toString()) {
                removeEventListener(xhr, listener);

                return resolve(msg.data);
            }

            return reject(msg);
        };
        // eslint-disable-next-line
        xhr.onreadystatechange = (evt: Event) => {
            if (evt === undefined || evt.currentTarget === undefined) {
                return;
            }
            const xhr = evt.currentTarget as XMLHttpRequest;

            if ((xhr.readyState === 2 || xhr.readyState === 4) && xhr.status >= 400) {
                if (errorCount.cur >= errorCount.max) {
                    removeEventListener(xhr, listener);

                    reject(format(Code.NetWorrError, evt, `${xhr.responseURL || url} ${xhr.status}`));
                }
                errorCount.cur++;
                xhr.abort();
                xhr.open(method, url, true);
                xhr.send();
            }
        };

        // eslint-disable-next-line
        xhr.onerror = (evt: Event) => {
            const xhr = evt.currentTarget as XMLHttpRequest;

            if (errorCount.cur >= errorCount.max) {
                removeEventListener(xhr, listener);

                return reject(format(Code.NetWorrError, evt, `${xhr.responseURL || url} ${xhr.status}`));
            }
            errorCount.cur++;
            xhr.abort();
            xhr.open(method, url, true);
            xhr.send();
        };
        xhr.open(method, url, true);
        xhr.send();
    });
}
