function arraybuffToBase64(buffer: ArrayBuffer): string {
    let binary = '';
    const bytes = new Uint8Array(buffer);
    const len = bytes.byteLength;

    for (let i = 0; i < len; i++) {
        binary += String.fromCharCode(bytes[i]);
    }

    return window.btoa(binary);
}

export function arraybuffToBase64Sync(buffer: ArrayBuffer): Promise<any> {
    return new Promise<any>((resolve, reject): void => {
        if (window.Blob && window.FileReader) {
            const blob = new Blob([buffer], { type: 'application/octet-binary' });
            const fileReader = new FileReader();

            fileReader.onerror = (e): void => {
                reject(e);
            };
            fileReader.onload = (): void => {
                const dataUrl = fileReader.result as string;

                const base64 = dataUrl.substr(dataUrl.indexOf(',') + 1);

                resolve(base64);
            };
            fileReader.readAsDataURL(blob);
        }
        else {
            resolve(arraybuffToBase64(buffer));
        }
    });
}

